import Vue from 'vue';
import FeedbackOverlay from './components/FeedbackOverlay.vue';

// prepare
$(function() {

  // create overlay element and mount it
  let overlayElement = document.createElement('div');
  overlayElement.setAttribute('id', 'feedbackOverlay');
  $('body').append(overlayElement);
  new Vue({
    el: 'div#feedbackOverlay',
    render: h => h(FeedbackOverlay)
  });
});



